import React from 'react';
import './container.scss';

const Container = () => {
    return (
        <div className={"about--container"}>
            <div className={"about--container__card orange"}>
                <h1 className={"about--container__card--heading"}>Web-fejlesztés</h1>
                <h3 className={"about--container__card--subheading"}>Én egy full-stack webfejlesztő vagyok, melynek jelentése hogy a weboldal kinézetét, illetve a weboldal funkcionalitását (pl.: adatbázis) én írom meg.</h3>
            </div>
            <div className={"about--container__card blue"}>
                <h1 className={"about--container__card--heading"}>Programozás</h1>
                <h3 className={"about--container__card--subheading"}>Programoztam már le játékot, illetve pár desktop-alkalmazást, és jelenleg tervezek egy mobil alkalmazást mely kiterjesztett valósággal dolgozik.</h3>
            </div>
            <div className={"about--container__card red"}>
                <h1 className={"about--container__card--heading"}>Etikus hackelés</h1>
                <h3 className={"about--container__card--subheading"}></h3>
            </div>
        </div>
    );
}

export default Container;